.loader {
  width: 50px;
  height: 50px;
  border: 5px solid rgba(255, 255, 255, .3);
  border-top-color: #000;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: none;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

